/* eslint-disable */
import ng from 'vendor-bb-angular';

import bbStorageModuleKey, { bbStorageServiceKey } from 'lib-bb-storage-ng';
import accountStatementsData from './data-bb-account-statements-http';

const accountStatementsDataModuleKey = 'data-bb-account-statements-http-ng';

export const accountStatementsDataKey = 'data-bb-account-statements-http-ng:accountStatementsData';

export default ng
  .module(accountStatementsDataModuleKey, [
    bbStorageModuleKey,
  ])
  .provider(accountStatementsDataKey, [() => {
    const config = {
      baseUri: '/',
    };

    return {
      setBaseUri: (baseUri) => {
        config.baseUri = baseUri;
      },
      $get: [
        '$q',
        bbStorageServiceKey,
        '$httpParamSerializer',
        /* into */
       accountStatementsData(config),
      ],
    };
  }])
  .name;



